:root {
  --primary: #2C3531;
  --secondary: #D1E8E2;
  --tertiary: #DA7B93;
  --light: #fafafa;
  --dark: #116466;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  overscroll-behavior: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: var(--dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
  font-weight: bold;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
  font-family: Arial, sans-serif;
}

a:hover {
  color: #DA7B93;
  text-shadow: 2px 2px 4px rgb(46, 226, 187);
}

.prodh2 {
  border-style: double;
  text-align: center;
  padding: 30px 0 30px 0;
  margin: 50px 0 50px 0;
  border-width: 5px 5px;
}

header {
  background: var(--primary);
  height: 150px;
  background-image: ('./thc-scifi.jpg');
}

header a {
  color: var(--light);
}

header h1 {
  text-align: center;
  padding-top: 30px;
  margin-top: 0px;
}

.navstyle ul {
  list-style-type: none;
  overflow: hidden;
}

.navstyle li {
  float: right;
}

.navstyle li a {
  display: block;
}

.catDiv {
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
}

.catButton {
  font-size: 24px;
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 5px;
  background-color: rgb(136, 199, 136);
}

button:hover {
  opacity: .8;
  box-shadow: 5px 5px 5px grey;
  background-image: linear-gradient(to right, lime, green)
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  height: 475px;
  text-align: center;
}

.imgCard {
  height: 200px;
}

.prodName {
  height: 50px;
}

.error-text {
  color: var(--error);
}

ul {
  color: var(--primary)
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
  .navstyle {
    margin-left: 0;
  }
  .catDiv {
    position: relative;
  }
  .catButton {
    display: inline-block;
    padding-top: 5px;
    margin-top: 5px;
    width: 50%;
    font-size: 18px;
  }
  .navstyle li {
    float: none;
  }
  
  .navstyle li a {
    display: block;
    text-align: center;
  }

  .cart {
    position: relative;
  }
}
